body {
  margin: 0;
  padding: 0;
}

.snowflake {
  color: #fff;
  font-size: 1em;
  position: fixed;
  top: -2em;
}
img {
  width: 100%;
}

.wrapper {
  width: 50%;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
}
.conteiner {
  /* position: relative; */
  display: flex;
  justify-content: start;
  align-items: center;
  flex-direction: column;
}
@media (max-width: 600px) {
  .wrapper {
    width: 100%;
    padding: 10px;
  }
  .secret-input {
    width: 70%;
  }
  .text-footer{
    margin-top: 5px;
    margin-left: 25px;
  }
}
